.popup__input {
  padding: 0;
  margin-bottom: 30px;
  border: none;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
  width: 100%;
  height: 27px;
  box-sizing: border-box;
  color: black;
  font-size: 14px;
  line-height: 17px;
  padding-bottom: 13px;
  outline: none;
}

.popup__input:last-child {
  margin-bottom: 48px;
}

@media screen and (max-width: 767px) {
  .popup__input:last-child {
    margin-bottom: 45px;
  }
}
